body {
    background-color: #484B4B;
}

h1 {
    font-family: sans-serif;
    color: #FFF;
    text-align: center;
    margin: 2rem 0;
    font-size: 4vw;
}

h3 {
    background-color: darkorange;
    border-radius: 2rem;
    padding-left: 1rem;
    padding-bottom: 0.5rem;
    padding-top: 0.5rem;
}

#content {
    background-color: #FFFFFF;
    border-radius: 1rem;
    margin: 2rem 5.5rem;
    padding: 2rem 6rem;
    max-width: 59%;
    width: 59%;
}

#homeContent {
    background-color: #FFFFFF;
    border-radius: 1rem;
    margin: 2rem 5.5rem;
    padding: 4rem 6rem 16rem 6rem;
    max-width: 59%;
    width: 59%;
}

#text{
    text-align: center;
}

.graphic{
    text-align: center;
    /*max-height: 600px;*/
}

#audio {
    text-align: center;
    padding-top: 2rem;
    padding-bottom: 1.5rem;
}

object {
    width: 10rem;
    margin-left: 42%;
}



