.tab {
    font-weight: bold;

    background-color: #111;
    color: #FFF;

    z-index: 2;

    margin: 0.5rem 0 0.5rem 3rem;
    padding: 1rem;
    display: inline-block;
    text-align: center;
    width: 5ch;

    border-radius: 0.2rem;
}

.active {
    font-weight: bold;

    background-color: #0099FF;
    color: #FFF;

    z-index: 2;

    margin: 0.5rem 0 0.5rem 3rem;
    padding: 1rem;
    display: inline-block;
    text-align: center;
    width: 5ch;

    border-radius: 0.2rem;
}

.tab:hover{
    background-color: #0099FF;
}

.tab:active {
    background-color: #007acc;
}



